			},
			onSelect(item) {
				// 默认情况下点击选项时不会自动收起
				// 可以通过 close-on-click-action 属性开启自动收起
				this.show = false;
				Toast(item.name);
			},
			confirm(){
				this.dataAddress="55"
			},
			// 切换地址
			onAddrConfirm (e) { // 点击确认，获取所选的省市区数据
			    // 确定选择,返回的必定是三元素数组
			    console.log('this.onAddrConfirm=>e', e)
